.tabs {
  font-size: 0;
  margin-left: auto;
  margin-right: auto;

  & > input[type="radio"] {
    display: none;
  }

  & > div {
    display: none;
    border-top: 1px solid var(--gray-tabs);
    padding: 42px 20px 40px 26px;
    font-size: 16px;
    background-color: var(--white-grey);
  }

  #tab-btn-1:checked ~ #content-1,
  #tab-btn-2:checked ~ #content-2,
  #tab-btn-3:checked ~ #content-3 {
    display: block;
  }

  & > label {
    display: inline-block;
    text-align: center;
    vertical-align: middle;
    user-select: none;
    background-color: var(--deep-white);
    padding: 2px 8px;
    font-size: 16px;
    line-height: 1.5;
    transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out;
    cursor: pointer;
    position: relative;
    top: 1px;
  }

  & > label:not(:first-of-type) {
    border-left: none;
  }

  & > input[type="radio"]:checked + label {
    background: var(--white-grey);
    border-bottom: 1px solid var(--white-grey);
  }

  & > input[type="radio"] + label {
    padding: 22px;
    background-color: var(--white);
    margin-right: 4px;
    font-weight: 400;
    font-size: 11px;
    line-height: 15px;
    text-transform: uppercase;
    border-radius: 2px 2px 0 0;
    color: var(--black);
    border: 1px solid var(--gray-tabs);
  }

  & > input[type="radio"]:first-of-type + label {
    margin-left: 24px;
  }
}
